import Vue from 'vue';
import { CButton, CCard, CCardBody, CForm, CInputFile } from '@coreui/vue/src';
import DaemonConfigurationService from '../../services/DaemonConfigurationService';
import { fileDownloader } from '../../helpers/fileDownloader';
export default Vue.extend({
    name: 'ConfigMigration',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CForm: CForm,
        CInputFile: CInputFile
    },
    data: function () {
        return {
            configEmpty: true,
            firstConfig: true,
        };
    },
    methods: {
        getFiles: function () {
            var input = this.$refs.configZip.$el.children[1];
            return input.files;
        },
        exportConfig: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            DaemonConfigurationService.export()
                .then(function (response) {
                var fileName = 'iqrf-gateway-configuration_' + new Date().toISOString();
                var file = fileDownloader(response, 'application/zip', fileName);
                _this.$store.commit('spinner/HIDE');
                file.click();
            });
        },
        importConfig: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            var files = this.getFiles();
            if (files === null || files.length === 0) {
                this.$toast.error(this.$t('config.migration.messages.importButton').toString());
                return;
            }
            DaemonConfigurationService.import(files[0])
                .then(function () {
                _this.$store.commit('spinner/HIDE');
                _this.$toast.success(_this.$t('config.migration.messages.imported').toString());
            })
                .catch(function (error) {
                _this.$store.commit('spinner/HIDE');
                if (error.response === undefined) {
                    console.error(error);
                    return;
                }
                if (error.response.status === 400) {
                    _this.$toast.error(_this.$t('config.migration.messages.invalidConfig').toString());
                }
                else if (error.response.status === 415) {
                    _this.$toast.error(_this.$t('config.migration.messages.invalidFormat').toString());
                }
            });
        },
        isEmpty: function () {
            if (this.firstConfig) {
                this.firstConfig = false;
            }
            var files = this.getFiles();
            this.configEmpty = files === null || files.length === 0;
        }
    },
    metaInfo: {
        title: 'config.migration.title',
    },
});
