import Vue from 'vue';
import { CButton, CButtonGroup } from '@coreui/vue/src';
export default Vue.extend({
    name: 'InterfaceMappings',
    components: {
        CButton: CButton,
        CButtonGroup: CButtonGroup,
    },
    props: {
        interfaceType: {
            type: String,
            required: true,
        },
    },
    data: function () {
        return {
            mappings: {
                'spi': require('../../../app/ConfigModule/json/SpiPins.json'),
                'uart': require('../../../app/ConfigModule/json/UartPins.json'),
            },
        };
    },
    methods: {
        setMapping: function (board) {
            var mapping = this.mappings[this.interfaceType][board];
            this.$emit('update-mapping', mapping);
        },
    },
});
