import Vue from 'vue';
import { CButton, CForm, CInput, CModal } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { required } from 'vee-validate/dist/rules';
import PixlaService from '../../services/PixlaService';
export default Vue.extend({
    name: 'PixlaTokenEditor',
    components: {
        CButton: CButton,
        CForm: CForm,
        CInput: CInput,
        CModal: CModal,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider,
    },
    props: {
        show: {
            type: Boolean,
            required: true,
        }
    },
    data: function () {
        return {
            token: '',
        };
    },
    created: function () {
        extend('required', required);
    },
    methods: {
        close: function () {
            this.$emit('update:show', false);
        },
        processSubmit: function () {
            var _this = this;
            PixlaService.setToken(this.token)
                .then(function () {
                _this.token = '';
                _this.$store.commit('spinner/SHOW');
                _this.$emit('update:show', false);
                _this.$emit('token-updated');
                _this.$toast.success(_this.$t('cloud.pixla.editModal.messages.success').toString());
            })
                .catch(function () {
                _this.$toast.error(_this.$t('cloud.pixla.editModal.messages.failure').toString());
            });
        }
    },
});
