import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardHeader, CForm, CInput, CInputFile } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { required } from 'vee-validate/dist/rules';
import FormErrorHandler from '../../helpers/FormErrorHandler';
import CloudService from '../../services/CloudService';
import ServiceService from '../../services/ServiceService';
export default Vue.extend({
    name: 'AwsCreator',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardHeader: CCardHeader,
        CForm: CForm,
        CInput: CInput,
        CInputFile: CInputFile,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider
    },
    data: function () {
        return {
            endpoint: null,
            serviceName: 'aws',
            certEmpty: true,
            keyEmpty: true,
            firstCert: true,
            firstKey: true,
        };
    },
    created: function () {
        extend('required', required);
    },
    methods: {
        buildRequest: function () {
            var formData = new FormData();
            formData.append('endpoint', this.endpoint);
            formData.append('certificate', this.$refs.awsFormCert.$el.children[1].files[0]);
            formData.append('privateKey', this.$refs.awsFormKey.$el.children[1].files[0]);
            return formData;
        },
        save: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            return CloudService.create(this.serviceName, this.buildRequest())
                .then(function () {
                _this.$store.commit('spinner/HIDE');
                _this.$toast.success(_this.$t('cloud.messages.success').toString());
            })
                .catch(function (error) {
                FormErrorHandler.cloudError(error);
                return Promise.reject(error);
            });
        },
        saveAndRestart: function () {
            var _this = this;
            this.save()
                .then(function () {
                _this.$store.commit('spinner/SHOW');
                ServiceService.restart('iqrf-gateway-daemon')
                    .then(function () {
                    _this.$store.commit('spinner/HIDE');
                    _this.$toast.success(_this.$t('service.iqrf-gateway-daemon.messages.restart')
                        .toString());
                })
                    .catch(function (error) {
                    FormErrorHandler.serviceError(error);
                });
            })
                .catch(function () { });
        },
        isEmpty: function (button) {
            if (button === 'cert') {
                if (this.firstCert) {
                    this.firstCert = false;
                }
                this.certEmpty = this.$refs.awsFormCert.$el.children[1].files.length === 0;
            }
            else {
                if (this.firstKey) {
                    this.firstKey = false;
                }
                this.keyEmpty = this.$refs.awsFormKey.$el.children[1].files.length === 0;
            }
        }
    }
});
