import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardFooter, CCardHeader, CForm, CInput } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { between, integer, required } from 'vee-validate/dist/rules';
import { timeout } from '../../helpers/timeout';
import StandardLightService, { StandardLight } from '../../services/DaemonApi/StandardLightService';
export default Vue.extend({
    name: 'LightManager',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardFooter: CCardFooter,
        CCardHeader: CCardHeader,
        CForm: CForm,
        CInput: CInput,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider
    },
    data: function () {
        return {
            address: 1,
            allowedMTypes: [
                'iqrfLight_Enumerate',
                'iqrfLight_SetPower',
                'iqrfLight_IncrementPower',
                'iqrfLight_DecrementPower'
            ],
            index: 0,
            lights: null,
            numLights: 0,
            power: 0,
            prevPower: 0,
            responseType: null,
            timeout: null,
        };
    },
    created: function () {
        var _this = this;
        extend('between', between);
        extend('integer', integer);
        extend('required', required);
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONSEND') {
                if (!_this.allowedMTypes.includes(mutation.payload.mType)) {
                    return;
                }
                _this.responseType = null;
                _this.timeout = timeout('iqrfnet.networkManager.messages.submit.timeout', 30000);
            }
            if (mutation.type === 'SOCKET_ONMESSAGE') {
                if (!_this.allowedMTypes.includes(mutation.payload.mType)) {
                    return;
                }
                clearTimeout(_this.timeout);
                _this.$store.commit('spinner/HIDE');
                switch (mutation.payload.data.status) {
                    case -1:
                        _this.$toast.error(_this.$t('iqrfnet.standard.light.messages.timeout').toString());
                        break;
                    case 0:
                        _this.$toast.success(_this.$t('iqrfnet.standard.light.messages.success').toString());
                        if (mutation.payload.mType === 'iqrfLight_Enumerate') {
                            _this.numLights = mutation.payload.data.rsp.result.lights;
                            _this.responseType = 'enum';
                        }
                        else {
                            _this.prevPower = mutation.payload.data.rsp.result.prevVals[0];
                            _this.responseType = 'power';
                        }
                        break;
                    case 3:
                        _this.$toast.error(_this.$t('iqrfnet.standard.light.messages.pnum').toString());
                        break;
                    default:
                        _this.$toast.error(_this.$t('iqrfnet.standard.light.messages.failure').toString());
                        break;
                }
            }
        });
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
    methods: {
        submitEnumerate: function () {
            this.$store.commit('spinner/SHOW');
            StandardLightService.enumerate(this.address);
        },
        submitGetPower: function () {
            this.$store.commit('spinner/SHOW');
            StandardLightService.getPower(this.address, this.index);
        },
        submitSetPower: function () {
            this.$store.commit('spinner/SHOW');
            StandardLightService.setPower(this.address, [new StandardLight(this.index, this.power)]);
        },
        submitIncrementPower: function () {
            this.$store.commit('spinner/SHOW');
            StandardLightService.incrementPower(this.address, [new StandardLight(this.index, this.power)]);
        },
        submitDecrementPower: function () {
            this.$store.commit('spinner/SHOW');
            StandardLightService.decrementPower(this.address, [new StandardLight(this.index, this.power)]);
        },
    }
});
