import Vue from 'vue';
import { CButton, CCard, CCardBody, CForm, CInput, CInputCheckbox, CModal, CSelect } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { timeout } from '../../helpers/timeout';
import { between, integer, required } from 'vee-validate/dist/rules';
import IqrfNetService from '../../services/IqrfNetService';
export default Vue.extend({
    name: 'BondingManager',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CForm: CForm,
        CInput: CInput,
        CInputCheckbox: CInputCheckbox,
        CModal: CModal,
        CSelect: CSelect,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider
    },
    data: function () {
        return {
            address: 1,
            autoAddress: false,
            bondMethod: 'local',
            bondingRetries: 1,
            modalClear: false,
            modalUnbond: false,
            unbondCoordinatorOnly: false,
            scCode: '',
            timeout: null,
        };
    },
    created: function () {
        var _this = this;
        extend('between', between);
        extend('integer', integer);
        extend('required', required);
        extend('scCode', function (code) {
            var regex = RegExp('^[a-zA-Z0-9]{34}$');
            return regex.test(code);
        });
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONSEND' &&
                mutation.payload.mType === ('iqmeshNetwork_BondNodeLocal' ||
                    'iqmeshNetwork_SmartConnect' || 'iqrfEmbedCoordinator_ClearAllBonds' ||
                    'iqrfEmbedCoordinator_RemoveBond')) {
                _this.timeout = timeout('iqrfnet.networkManager.messages.submit.timeout', 30000);
            }
            if (mutation.type === 'SOCKET_ONMESSAGE') {
                if (mutation.payload.mType === 'iqmeshNetwork_BondNodeLocal' ||
                    mutation.payload.mType === 'iqmeshNetwork_SmartConnect') {
                    clearTimeout(_this.timeout);
                    _this.$store.commit('spinner/HIDE');
                    switch (mutation.payload.data.status) {
                        case -1:
                            _this.$toast.error(_this.$t('iqrfnet.networkManager.messages.submit.timeout')
                                .toString());
                            break;
                        case 0:
                            _this.$toast.success(_this.$t('iqrfnet.networkManager.messages.submit.bonding.success')
                                .toString());
                            _this.$emit('update-devices');
                            break;
                        default:
                            _this.$toast.error(_this.$t('iqrfnet.networkManager.messages.submit.bonding.error_fail')
                                .toString());
                            break;
                    }
                }
                else if (mutation.payload.mType === 'iqrfEmbedCoordinator_ClearAllBonds' ||
                    mutation.payload.mType === 'iqmeshNetwork_RemoveBond') {
                    clearTimeout(_this.timeout);
                    _this.$store.commit('spinner/HIDE');
                    switch (mutation.payload.data.status) {
                        case -1:
                            _this.$toast.error(_this.$t('iqrfnet.networkManager.messages.submit.timeout')
                                .toString());
                            break;
                        case 0:
                            if (mutation.payload.data.rsp.nodesNr === 0) {
                                _this.$toast.success(_this.$t('iqrfnet.networkManager.messages.submit.removeBond.clearSuccess')
                                    .toString());
                            }
                            else {
                                _this.$toast.success(_this.$t('iqrfnet.networkManager.messages.submit.removeBond.success')
                                    .toString());
                            }
                            _this.$emit('update-devices');
                            break;
                        case 1002:
                            _this.$toast.error(_this.$t('iqrfnet.networkManager.messages.submit.removeBond.remove_error')
                                .toString());
                            break;
                        default:
                            _this.$toast.error(_this.$t('iqrfnet.networkManager.messages.submit.removeBond.error_fail')
                                .toString());
                            break;
                    }
                }
                else if (mutation.payload.mType === 'messageError') {
                    clearTimeout(_this.timeout);
                    _this.$store.commit('spinner/HIDE');
                    _this.$toast.error(_this.$t('iqrfnet.networkManager.messages.submit.invalidMessage')
                        .toString());
                }
            }
        });
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
    methods: {
        processSubmitBond: function () {
            this.$store.commit('spinner/SHOW');
            if (this.bondMethod === 'local') {
                if (this.autoAddress) {
                    IqrfNetService.bondLocal(0);
                }
                else {
                    IqrfNetService.bondLocal(this.address);
                }
            }
            else if (this.bondMethod === 'smartConnect') {
                if (this.autoAddress) {
                    IqrfNetService.bondSmartConnect(0, this.scCode, this.bondingRetries);
                }
                else {
                    IqrfNetService.bondSmartConnect(this.address, this.scCode, this.bondingRetries);
                }
            }
        },
        processSubmitUnbond: function () {
            this.modalUnbond = false;
            this.$store.commit('spinner/SHOW');
            IqrfNetService.removeBond(this.address, this.unbondCoordinatorOnly);
        },
        processSubmitClearAll: function () {
            this.modalClear = false;
            this.$store.commit('spinner/SHOW');
            IqrfNetService.clearAllBonds(this.unbondCoordinatorOnly);
        }
    }
});
