import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardHeader, CForm, CSelect } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { required } from 'vee-validate/dist/rules';
import { FileFormat } from '../../iqrfNet/fileFormat';
import DpaService, { RFMode } from '../../services/IqrfRepository/DpaService';
import OsService from '../../services/DaemonApi/OsService';
import NativeUploadService from '../../services/NativeUploadService';
export default Vue.extend({
    name: 'DpaUpdater',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardHeader: CCardHeader,
        CForm: CForm,
        CSelect: CSelect,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider,
    },
    data: function () {
        return {
            address: 0,
            osBuild: undefined,
            trType: null,
            version: undefined,
            versions: [],
            requestSent: false,
        };
    },
    created: function () {
        var _this = this;
        extend('required', required);
        if (this.$store.state.webSocketClient.socket.isConnected) {
            this.getOsInfo();
        }
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONOPEN' &&
                _this.osBuild === undefined) {
                _this.getOsInfo();
                return;
            }
            if (mutation.type !== 'SOCKET_ONMESSAGE') {
                return;
            }
            if (mutation.payload.mType === 'iqrfEmbedOs_Read') {
                _this.$store.dispatch('spinner/hide');
                if (mutation.payload.data.status === 0) {
                    _this.handleResponse(mutation.payload);
                }
                else {
                    _this.$toast.error(_this.$t('iqrfnet.trUpload.messages.osInfoFail').toString());
                }
            }
            else if (mutation.payload.mType === 'mngDaemon_Upload') {
                if (!_this.requestSent) {
                    return;
                }
                _this.requestSent = false;
                _this.$store.dispatch('spinner/hide');
                clearTimeout(_this.timeout);
                if (mutation.payload.data.status === 0) {
                    _this.$toast.success(_this.$t('iqrfnet.trUpload.messages.success').toString());
                }
                else {
                    _this.$toast.error(_this.$t('iqrfnet.trUpload.messages.failure').toString());
                }
            }
        });
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
    methods: {
        convertVersion: function (version) {
            return Number.parseInt(version).toString(16).padStart(4, '0').toUpperCase();
        },
        getOsInfo: function () {
            this.$store.dispatch('spinner/show', { timeout: 10000 });
            this.requestSent = true;
            OsService.sendRead(this.address);
        },
        handleResponse: function (response) {
            var _this = this;
            var result = response.data.rsp.result;
            this.osBuild = this.convertVersion(result.osBuild);
            this.trType = result.trMcuType;
            this.version = this.convertVersion(result.dpaVer);
            DpaService.getVersions(this.osBuild)
                .then(function (versions) {
                for (var _i = 0, versions_1 = versions; _i < versions_1.length; _i++) {
                    var version = versions_1[_i];
                    var dpaVer = Number.parseInt(version.getVersion(false));
                    if (dpaVer < 400) {
                        _this.versions.push({
                            value: version.getVersion(false) + '-' + RFMode.LP,
                            label: version.getVersion(true) + ', ' + RFMode.LP + ' RF mode'
                        });
                        _this.versions.push({
                            value: version.getVersion(false) + '-' + RFMode.STD,
                            label: version.getVersion(true) + ', ' + RFMode.STD + ' RF mode'
                        });
                    }
                    else {
                        _this.versions.push({
                            value: version.getVersion(false),
                            label: version.getVersion(true),
                        });
                    }
                }
                _this.versions.sort().reverse();
            })
                .catch(function () {
                _this.$toast.error(_this.$t('iqrfnet.trUpload.messages.osBuildFail').toString());
            });
        },
        upload: function () {
            var _this = this;
            var request = {
                'osBuild': this.osBuild,
                'trSeries': this.trType,
            };
            if (this.version.endsWith('-STD')) {
                Object.assign(request, { 'dpa': this.version.split('-')[0] });
                Object.assign(request, { 'rfMode': RFMode.STD });
            }
            else if (this.version.endsWith('-LP')) {
                Object.assign(request, { 'dpa': this.version.split('-')[0] });
                Object.assign(request, { 'rfMode': RFMode.LP });
            }
            else {
                Object.assign(request, { 'dpa': this.version });
            }
            DpaService.getDpaFile(request)
                .then(function (response) {
                _this.$store.dispatch('spinner/show', { timeout: 30000 });
                _this.requestSent = true;
                NativeUploadService.upload(response.data.fileName, FileFormat.IQRF);
            })
                .catch(function (error) {
                _this.$store.commit('spinner/HIDE');
                if (error.response !== null) {
                    switch (error.response.status) {
                        case 400:
                            _this.$toast.error(_this.$t('iqrfnet.trUpload.messagess.badRequest').toString());
                            break;
                        case 404:
                            _this.$toast.error(_this.$t('iqrfnet.trUpload.messages.notFound').toString());
                            break;
                        case 500: {
                            var msg = error.response.data.message;
                            if (msg === 'Filesystem failure') {
                                _this.$toast.error(_this.$t('iqrfnet.trUpload.messagess.moveFailure').toString());
                            }
                            else if (msg === 'Download failure') {
                                _this.$toast.error(_this.$t('iqrfnet.trUpload.messagess.downloadFailure').toString());
                            }
                            else {
                                _this.$toast.error(_this.$t('iqrfnet.trUpload.messagess.genericError').toString());
                            }
                            break;
                        }
                    }
                }
                else {
                    _this.$toast.error(_this.$t('iqrfnet.trUpload.messagess.genericError').toString());
                }
            });
        },
    },
});
