import Vue from 'vue';
import { CButton, CCard, CForm, CInput, CSelect } from '@coreui/vue/src';
import { required, } from 'vee-validate/dist/rules';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import UserService from '../../services/UserService';
export default Vue.extend({
    name: 'UserEdit',
    components: {
        CButton: CButton,
        CCard: CCard,
        CForm: CForm,
        CInput: CInput,
        CSelect: CSelect,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider,
    },
    props: {
        userId: {
            type: Number,
            required: true,
        }
    },
    data: function () {
        return {
            loaded: false,
            username: null,
            language: null,
            role: null,
            oldPassword: null,
            newPassword: null,
        };
    },
    created: function () {
        var _this = this;
        extend('required', required);
        this.$store.commit('spinner/SHOW');
        UserService.get(this.userId)
            .then(function (response) {
            _this.loaded = true;
            _this.username = response.data.username;
            _this.language = response.data.language;
            _this.role = response.data.role;
            _this.$store.commit('spinner/HIDE');
        })
            .catch(function (error) {
            _this.loaded = false;
            _this.$store.commit('spinner/HIDE');
            if (error.response === undefined) {
                return;
            }
            if (error.response.status === 404) {
                _this.$router.push('/user/');
                _this.$toast.error(_this.$t('core.user.messages.notFound').toString());
            }
        });
    },
    methods: {
        handleSubmit: function () {
            var _this = this;
            if (this.$store.getters['user/getId'] === this.userId &&
                this.oldPassword !== null && this.newPassword !== null) {
                UserService.changePassword(this.oldPassword, this.newPassword)
                    .then(function () {
                    _this.performEdit();
                    _this.signOut();
                })
                    .catch(function () {
                    _this.$toast.error(_this.$t('core.user.messages.invalid.oldPassword').toString());
                });
            }
            else {
                this.performEdit();
                if (this.$store.getters['user/getId'] === this.userId) {
                    this.signOut();
                }
            }
        },
        performEdit: function () {
            var _this = this;
            return UserService.edit(this.userId, {
                username: this.username,
                language: this.language,
                role: this.role,
            })
                .then(function () {
                _this.$router.push('/user/');
                _this.$toast.success(_this.$t('core.user.messages.edit.success', { username: _this.username })
                    .toString());
            })
                .catch(function (error) {
                if (error.response === undefined) {
                    return;
                }
                if (error.response.status === 409) {
                    _this.$toast.error(_this.$t('core.user.messages.conflict.username').toString());
                }
            });
        },
        signOut: function () {
            var _this = this;
            this.$store.dispatch('user/signOut')
                .then(function () {
                _this.$router.push('/sign/in');
            });
        }
    },
    metaInfo: {
        title: 'core.user.edit.title',
    },
});
