import Vue from 'vue';
import { CButtonGroup, CCard, CCardBody, CCardHeader, CDropdown, CDropdownItem } from '@coreui/vue/src';
import IqrfService from '../../services/IqrfService';
export default Vue.extend({
    name: 'DpaMacros',
    components: {
        CButtonGroup: CButtonGroup,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardHeader: CCardHeader,
        CDropdown: CDropdown,
        CDropdownItem: CDropdownItem,
    },
    data: function () {
        return {
            macros: null,
        };
    },
    created: function () {
        var _this = this;
        IqrfService.getMacros()
            .then(function (response) {
            _this.macros = response.data.filter(function (group) {
                if (!group.enabled) {
                    return null;
                }
                group.macros = group.macros.filter(function (packet) {
                    if (packet.enabled) {
                        return packet;
                    }
                });
                return group;
            });
        });
    },
});
