import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardFooter, CCardHeader, CForm, CInput } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { between, integer, required } from 'vee-validate/dist/rules';
import { timeout } from '../../helpers/timeout';
import StandardSensorService from '../../services/DaemonApi/StandardSensorService';
export default Vue.extend({
    name: 'SensorManager',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardFooter: CCardFooter,
        CCardHeader: CCardHeader,
        CForm: CForm,
        CInput: CInput,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider
    },
    data: function () {
        return {
            address: 1,
            allowedMTypes: [
                'iqrfSensor_Enumerate',
                'iqrfSensor_ReadSensorsWithTypes'
            ],
            responseType: null,
            sensors: null,
            timeout: null
        };
    },
    created: function () {
        var _this = this;
        extend('between', between);
        extend('integer', integer);
        extend('required', required);
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONSEND') {
                if (!_this.allowedMTypes.includes(mutation.payload.mType)) {
                    return;
                }
                _this.responseType = null;
                _this.timeout = timeout('iqrfnet.networkManager.messages.submit.timeout', 30000);
            }
            if (mutation.type === 'SOCKET_ONMESSAGE') {
                if (!_this.allowedMTypes.includes(mutation.payload.mType)) {
                    return;
                }
                clearTimeout(_this.timeout);
                _this.$store.commit('spinner/HIDE');
                switch (mutation.payload.data.status) {
                    case -1:
                        _this.$toast.error(_this.$t('iqrfnet.standard.sensor.messages.timeout').toString());
                        break;
                    case 0:
                        _this.$toast.success(_this.$t('iqrfnet.standard.sensor.messages.success').toString());
                        if (mutation.payload.mType === 'iqrfSensor_Enumerate') {
                            _this.parseEnumerate(mutation.payload.data.rsp.result.sensors);
                            _this.responseType = 'enum';
                        }
                        else if (mutation.payload.mType === 'iqrfSensor_ReadSensorsWithTypes') {
                            _this.parseReadAll(mutation.payload.data.rsp.result.sensors);
                            _this.responseType = 'read';
                        }
                        break;
                    case 3:
                        _this.$toast.error(_this.$t('iqrfnet.standard.sensor.messages.pnum').toString());
                        break;
                    default:
                        _this.$toast.error(_this.$t('iqrfnet.standard.sensor.messages.failure').toString());
                        break;
                }
            }
        });
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
    methods: {
        parseEnumerate: function (sensors) {
            var _this = this;
            this.sensors = [];
            sensors.forEach(function (item) {
                if (item.id === 'BINARYDATA7') {
                    item = item.breakdown[0];
                }
                _this.sensors.push({ 'type': item.name, 'unit': item.unit });
            });
        },
        parseReadAll: function (sensors) {
            var _this = this;
            this.sensors = [];
            sensors.forEach(function (item) {
                if (item.id === 'BINARYDATA7') {
                    item = item.breakdown[0];
                }
                _this.sensors.push({ 'type': item.name, 'value': item.value, 'unit': item.unit });
            });
        },
        submitReadAll: function () {
            this.$store.commit('spinner/SHOW');
            StandardSensorService.readAll(this.address);
        },
        submitEnumerate: function () {
            this.$store.commit('spinner/SHOW');
            StandardSensorService.enumerate(this.address);
        },
    }
});
