import Vue from 'vue';
import { CButton, CCardBody, CForm, CInput } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { required } from 'vee-validate/dist/rules';
import UserService from '../../services/UserService';
export default Vue.extend({
    name: 'InstallCreateUser',
    components: {
        CButton: CButton,
        CCardBody: CCardBody,
        CForm: CForm,
        CInput: CInput,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider,
    },
    data: function () {
        return {
            username: null,
            password: null,
            language: 'en',
            role: 'normal',
        };
    },
    created: function () {
        extend('required', required);
    },
    methods: {
        handleSubmit: function () {
            var _this = this;
            UserService.add(this.username, this.password, this.language, this.role)
                .then(function () {
                _this.$router.push('/sign/in/');
                _this.$toast.success(_this.$t('core.user.messages.add.success', { username: _this.username })
                    .toString());
            }).catch(function (error) {
                if (error.response === undefined) {
                    return;
                }
            });
        },
    },
    metaInfo: {
        title: 'install.createUser.title',
    },
});
