import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardHeader, CCol, CForm, CInput, CInputCheckbox, CRow } from '@coreui/vue/src';
import DpaMacros from '../../components/IqrfNet/DpaMacros.vue';
import { between, integer, min_value, required } from 'vee-validate/dist/rules';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import sendPacket from '../../iqrfNet/sendPacket';
import { PrismEditor } from 'vue-prism-editor';
import 'vue-prism-editor/dist/prismeditor.min.css';
// @ts-ignore
import Prism from 'prismjs/components/prism-core';
import 'prismjs/components/prism-json';
import 'prismjs/themes/prism.css';
export default Vue.extend({
    name: 'SendDpaPacket',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardHeader: CCardHeader,
        CCol: CCol,
        CForm: CForm,
        CInput: CInput,
        CInputCheckbox: CInputCheckbox,
        CRow: CRow,
        DpaMacros: DpaMacros,
        PrismEditor: PrismEditor,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider,
    },
    data: function () {
        return {
            packet: null,
            address: 0,
            addressOverwrite: false,
            timeout: 1000,
            timeoutOverwrite: false,
            request: null,
            response: null,
        };
    },
    created: function () {
        var _this = this;
        extend('between', between);
        extend('integer', integer);
        extend('min', min_value);
        extend('required', required);
        extend('dpaPacket', function (packet) {
            return sendPacket.validatePacket(packet) ? true : 'iqrfnet.sendPacket.form.messages.invalid.packet';
        });
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONSEND' &&
                mutation.payload.mType === 'iqrfRaw') {
                _this.request = JSON.stringify(mutation.payload, null, 4);
                _this.response = null;
            }
            if (mutation.type === 'SOCKET_ONMESSAGE' &&
                mutation.payload.mType === 'iqrfRaw') {
                _this.$store.commit('spinner/HIDE');
                _this.response = JSON.stringify(mutation.payload, null, 4);
                switch (mutation.payload.data.status) {
                    case 0:
                        _this.$toast.success(_this.$t('iqrfnet.sendPacket.messages.success').toString());
                        break;
                    case 2:
                        _this.$toast.error(_this.$t('iqrfnet.sendPacket.messages.incorrect.pcmd').toString());
                        break;
                    case 3:
                        _this.$toast.error(_this.$t('iqrfnet.sendPacket.messages.incorrect.pnum').toString());
                        break;
                    case 5:
                        _this.$toast.error(_this.$t('iqrfnet.sendPacket.messages.incorrect.dataLength').toString());
                        break;
                    case 6:
                        _this.$toast.error(_this.$t('iqrfnet.sendPacket.messages.incorrect.data').toString());
                        break;
                    case 7:
                        _this.$toast.error(_this.$t('iqrfnet.sendPacket.messages.incorrect.hwpid').toString());
                        break;
                    case 8:
                        _this.$toast.error(_this.$t('iqrfnet.sendPacket.messages.incorrect.nadr').toString());
                        break;
                    default:
                        _this.$toast.error(_this.$t('iqrfnet.sendPacket.messages.failure').toString());
                        break;
                }
            }
        });
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
    methods: {
        /**
         * Handles Send DPA packet form submit event
         */
        handleSubmit: function () {
            if (this.packet === null || this.packet === '') {
                this.$toast.error(this.$t('iqrfnet.sendPacket.form.messages.missing.packet').toString());
                return;
            }
            this.$store.commit('spinner/SHOW');
            var json = {
                'mType': 'iqrfRaw',
                'data': {
                    'req': {
                        'rData': this.packet,
                    },
                    'returnVerbose': true,
                },
            };
            if (this.addressOverwrite && this.address !== null) {
                json.data.req.rData = sendPacket.updateNadr(this.packet, this.address);
            }
            if (this.timeoutOverwrite && this.timeout !== null) {
                json.data.timeout = this.timeout;
            }
            return this.$store.dispatch('sendRequest', json);
        },
        /**
         * JSON highlighter method
         */
        highlighter: function (code) {
            return Prism.highlight(code, Prism.languages.json, 'json');
        },
        /**
         * Sets new DPA packet
         * @param {string} newPacket New DPA packet
         */
        setPacket: function (newPacket) {
            this.packet = newPacket;
            this.setTimeout();
        },
        /**
         * Sets DPA timeout
         */
        setTimeout: function () {
            var packet = sendPacket.Packet.parse(this.packet);
            var newTimeout = packet.detectTimeout();
            if (newTimeout === null) {
                this.timeoutOverwrite = false;
                this.timeout = 1000;
            }
            else {
                this.timeoutOverwrite = true;
                this.timeout = newTimeout;
            }
        },
    },
    metaInfo: {
        title: 'iqrfnet.sendPacket.title',
    },
});
