import Vue from 'vue';
import { CButton, CCard } from '@coreui/vue/src';
import PixlaTokenEditor from '../../components/Cloud/PixlaTokenEditor.vue';
import PixlaService from '../../services/PixlaService';
import ServiceService from '../../services/ServiceService';
export default Vue.extend({
    name: 'PixlaControl',
    components: {
        CButton: CButton,
        CCard: CCard,
        PixlaTokenEditor: PixlaTokenEditor,
    },
    data: function () {
        return {
            showEditor: false,
            serviceName: 'gwman-client',
            token: null,
            missing: false,
            unsupported: false,
            service: undefined,
        };
    },
    created: function () {
        this.$store.commit('spinner/SHOW');
        this.getToken();
        this.getStatus();
    },
    methods: {
        enable: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            ServiceService.enable(this.serviceName)
                .then(function () {
                _this.getStatus();
                _this.$toast.success(_this.$t('service.' + _this.serviceName + '.messages.enable')
                    .toString());
            })
                .catch(this.handleError);
        },
        disable: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            ServiceService.disable(this.serviceName)
                .then(function () {
                _this.getStatus();
                _this.$toast.success(_this.$t('service.' + _this.serviceName + '.messages.disable')
                    .toString());
            })
                .catch(this.handleError);
        },
        getStatus: function () {
            var _this = this;
            ServiceService.getStatus(this.serviceName)
                .then(function (status) {
                _this.service = status;
                _this.unsupported = false;
                _this.$store.commit('spinner/HIDE');
            })
                .catch(this.handleError);
        },
        getToken: function () {
            var _this = this;
            PixlaService.getToken()
                .then(function (token) {
                _this.token = token;
                _this.$store.commit('spinner/HIDE');
            })
                .catch(function () {
                _this.token = null;
                _this.$store.commit('spinner/HIDE');
            });
        },
        handleError: function (error) {
            this.$store.commit('spinner/HIDE');
            if (error.response === undefined) {
                return;
            }
            var response = error.response;
            if (response.status === 404) {
                this.missing = true;
                this.$toast.error(this.$t('service.errors.missingService').toString());
            }
            if (response.status === 500 &&
                response.data.message === 'Unsupported init system') {
                this.unsupported = false;
                this.$toast.error(this.$t('service.errors.unsupportedInit').toString());
            }
        },
        restart: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            ServiceService.restart(this.serviceName)
                .then(function () {
                _this.getStatus();
                _this.$toast.success(_this.$t('service.' + _this.serviceName + '.messages.restart')
                    .toString());
            })
                .catch(this.handleError);
        },
        start: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            ServiceService.start(this.serviceName)
                .then(function () {
                _this.getStatus();
                _this.$toast.success(_this.$t('service.' + _this.serviceName + '.messages.start')
                    .toString());
            })
                .catch(this.handleError);
        },
        stop: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            ServiceService.stop(this.serviceName)
                .then(function () {
                _this.getStatus();
                _this.$toast.success(_this.$t('service.' + _this.serviceName + '.messages.stop')
                    .toString());
            })
                .catch(this.handleError);
        },
    },
    beforeRouteEnter: function (to, from, next) {
        next(function (vm) {
            if (!vm.$store.getters['features/isEnabled']('pixla')) {
                vm.$toast.error(vm.$t('cloud.pixla.messages.disabled').toString());
                vm.$router.push(from.path);
            }
        });
    },
    metaInfo: {
        title: 'cloud.pixla.title',
    },
});
