import Vue from 'vue';
import { CButton, CCard } from '@coreui/vue/src';
import GatewayService from '../../services/GatewayService';
import { fileDownloader } from '../../helpers/fileDownloader';
export default Vue.extend({
    name: 'DaemonLogViewer',
    components: {
        CButton: CButton,
        CCard: CCard,
    },
    data: function () {
        return {
            log: null
        };
    },
    created: function () {
        var _this = this;
        this.$store.commit('spinner/SHOW');
        GatewayService.getLatestLog()
            .then(function (response) {
            _this.log = response.data;
            _this.$store.commit('spinner/HIDE');
        })
            .catch(function (error) {
            _this.$store.commit('spinner/HIDE');
            if (error.response) {
                if (error.response.data.code === 500) {
                    _this.$toast.error(_this.$t('gateway.log.messages.notFound').toString());
                }
            }
        });
    },
    methods: {
        downloadArchive: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            GatewayService.getLogArchive().then(function (response) {
                var file = fileDownloader(response, 'application/zip', 'iqrf-gateway-logs.zip');
                _this.$store.commit('spinner/HIDE');
                file.click();
            }).catch(function () { return (_this.$store.commit('spinner/HIDE')); });
        }
    },
    metaInfo: {
        title: 'gateway.log.title',
    },
});
