import Vue from 'vue';
import { CButton, CCard, CForm, CInput, CInputCheckbox, CSelect } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { between, integer, required } from 'vee-validate/dist/rules';
import FormErrorHandler from '../../helpers/FormErrorHandler';
import FeatureConfigService from '../../services/FeatureConfigService';
export default Vue.extend({
    name: 'ControllerConfig',
    components: {
        CButton: CButton,
        CCard: CCard,
        CForm: CForm,
        CInput: CInput,
        CInputCheckbox: CInputCheckbox,
        CSelect: CSelect,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider
    },
    data: function () {
        return {
            name: 'controller',
            config: null,
        };
    },
    created: function () {
        extend('between', between);
        extend('integer', integer);
        extend('required', required);
        extend('ws_addr', function (addr) {
            var regex = RegExp('^ws:\\/\\/.+:([1-9]|[1-9][0-9]|[1-9][0-9]{2}|[1-9][0-9]{3}|[1-4][0-9][0-1][0-5][0-1])$');
            return regex.test(addr);
        });
        this.getConfig();
    },
    methods: {
        getConfig: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            FeatureConfigService.getConfig(this.name)
                .then(function (response) {
                _this.$store.commit('spinner/HIDE');
                _this.config = response.data;
            })
                .catch(function (error) {
                FormErrorHandler.configError(error);
            });
        },
        processSubmit: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            FeatureConfigService.saveConfig(this.name, this.config)
                .then(function () {
                _this.$store.commit('spinner/HIDE');
                _this.$toast.success(_this.$t('forms.messages.saveSuccess').toString());
            })
                .catch(function (error) {
                FormErrorHandler.configError(error);
            });
        },
    },
    beforeRouteEnter: function (to, from, next) {
        next(function (vm) {
            if (!vm.$store.getters['features/isEnabled']('iqrfGatewayController')) {
                vm.$toast.error(vm.$t('controllerConfig.messages.disabled').toString());
                vm.$router.push(from.path);
            }
        });
    },
    metaInfo: {
        title: 'controllerConfig.description',
    },
});
