import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardHeader, CForm, CInputFile, CSelect } from '@coreui/vue/src';
import { FileFormat } from '../../iqrfNet/fileFormat';
import NativeUploadService from '../../services/NativeUploadService';
export default Vue.extend({
    name: 'FileUpload',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardHeader: CCardHeader,
        CForm: CForm,
        CInputFile: CInputFile,
        CSelect: CSelect
    },
    data: function () {
        return {
            fileEmpty: true,
            fileUntouched: true,
            selectOptions: [
                { value: null, label: this.$t('iqrfnet.trUpload.messages.fileFormat') },
                { value: FileFormat.HEX, label: this.$t('iqrfnet.trUpload.fileFormats.hex') },
                { value: FileFormat.IQRF, label: this.$t('iqrfnet.trUpload.fileFormats.iqrf') },
                { value: FileFormat.TRCNFG, label: this.$t('iqrfnet.trUpload.fileFormats.trcnfg') }
            ],
            failed: false,
            format: null,
            requestSent: false,
            timeout: null,
        };
    },
    created: function () {
        var _this = this;
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONSEND') {
                if (!_this.requestSent) {
                    return;
                }
                if (mutation.payload.mType !== 'mngDaemon_Upload') {
                    return;
                }
                _this.timeout = setTimeout(function () {
                    _this.$toast.error(_this.$t('iqrfnet.trUpload.messages.timeout').toString());
                    _this.failed = true;
                }, 30000);
            }
            if (mutation.type === 'SOCKET_ONMESSAGE') {
                if (_this.failed) {
                    return;
                }
                if (mutation.payload.mType === 'mngDaemon_Upload') {
                    if (!_this.requestSent) {
                        return;
                    }
                    _this.requestSent = false;
                    _this.$store.dispatch('spinner/hide');
                    clearTimeout(_this.timeout);
                    if (mutation.payload.data.status === 0) {
                        _this.$toast.success(_this.$t('iqrfnet.trUpload.messages.success').toString());
                    }
                    else {
                        _this.$toast.error(_this.$t('iqrfnet.trUpload.messages.failure').toString());
                    }
                }
            }
        });
    },
    beforeDestroy: function () {
        clearTimeout(this.timeout);
        this.unsubscribe();
    },
    methods: {
        getFiles: function () {
            var input = this.$refs.fileUpload.$el.children[1];
            return input.files;
        },
        submitUpload: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            var formData = new FormData();
            if (this.$store.getters['user/getRole'] === 'power' && this.format !== null) {
                formData.append('format', this.format);
            }
            var files = this.getFiles();
            if (files == null || files.length === 0) {
                this.$toast.error(this.$t('iqrfnet.trUpload.messages.file').toString());
                return;
            }
            formData.append('file', this.getFiles()[0]);
            this.requestSent = true;
            NativeUploadService.uploadREST(formData)
                .then(function (response) {
                _this.$store.dispatch('spinner/show', { timeout: 30000 });
                NativeUploadService.upload(response.data.fileName, response.data.format);
            })
                .catch(function () {
                _this.$store.commit('spinner/HIDE');
                _this.$toast.error(_this.$t('iqrfnet.trUpload.messages.failure').toString());
            });
        },
        isEmpty: function () {
            if (this.fileUntouched) {
                this.fileUntouched = false;
            }
            var files = this.getFiles();
            this.fileEmpty = files === null || files.length === 0;
        }
    },
});
