import Vue from 'vue';
import { CButton, CCard } from '@coreui/vue/src';
import DaemonModeService, { DaemonMode } from '../../services/DaemonModeService';
export default Vue.extend({
    name: 'DaemonMode',
    components: {
        CButton: CButton,
        CCard: CCard,
    },
    data: function () {
        return {
            loaded: false,
            mode: DaemonMode.unknown,
            unsubscribe: function () { },
        };
    },
    created: function () {
        var _this = this;
        if (this.$store.state.webSocketClient.socket.isConnected) {
            this.getMode();
        }
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONOPEN' &&
                _this.mode === DaemonMode.unknown) {
                _this.getMode();
                return;
            }
            if (mutation.type !== 'SOCKET_ONMESSAGE' ||
                mutation.payload.mType !== 'mngDaemon_Mode') {
                return;
            }
            _this.handleResponse(mutation.payload);
        });
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
    methods: {
        handleResponse: function (response) {
            this.mode = DaemonModeService.parse(response);
            this.$store.commit('spinner/HIDE');
            if (this.mode === DaemonMode.unknown) {
                this.$toast.error(this.$t('gateway.mode.messages.' + this.loaded ? 'set' : 'get')
                    .toString());
            }
            else if (this.loaded) {
                this.$toast.success(this.$t('gateway.mode.messages.' + this.mode).toString());
            }
            else {
                this.loaded = true;
            }
        },
        getMode: function () {
            this.$store.dispatch('spinner/show', { timeout: 10000 });
            DaemonModeService.get();
        },
        setMode: function (newMode) {
            this.$store.dispatch('spinner/hide');
            DaemonModeService.set(newMode);
        },
    },
    metaInfo: {
        title: 'gateway.mode.title',
    },
});
