import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardFooter, CCardHeader, CForm, CInput } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { timeout } from '../../helpers/timeout';
import { between, integer, required } from 'vee-validate/dist/rules';
import StandardDaliService from '../../services/DaemonApi/StandardDaliService';
export default Vue.extend({
    name: 'DaliManager',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardFooter: CCardFooter,
        CCardHeader: CCardHeader,
        CForm: CForm,
        CInput: CInput,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider
    },
    data: function () {
        return {
            address: 1,
            answers: null,
            commands: [null],
            responseReceived: false,
            timeout: null
        };
    },
    created: function () {
        var _this = this;
        extend('between', between);
        extend('integer', integer);
        extend('required', required);
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONSEND') {
                if (mutation.payload.mType === 'iqrfDali_SendCommands') {
                    _this.timeout = timeout('iqrfnet.networkManager.messages.submit.timeout', 30000);
                }
            }
            if (mutation.type === 'SOCKET_ONMESSAGE') {
                if (mutation.payload.mType === 'iqrfDali_SendCommands') {
                    clearTimeout(_this.timeout);
                    _this.$store.commit('spinner/HIDE');
                    switch (mutation.payload.data.status) {
                        case -1:
                            _this.$toast.error(_this.$t('iqrfnet.standard.dali.messages.timeout').toString());
                            break;
                        case 0:
                            _this.$toast.success(_this.$t('iqrfnet.standard.dali.messages.success').toString());
                            _this.answers = mutation.payload.data.rsp.result.answers;
                            _this.responseReceived = true;
                            break;
                        case 3:
                            _this.$toast.error(_this.$t('iqrfnet.standard.dali.messages.pnum').toString());
                            break;
                        default:
                            _this.$toast.error(_this.$t('iqrfnet.standard.dali.messages.failure').toString());
                            break;
                    }
                }
            }
        });
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
    methods: {
        addDaliCommand: function () {
            this.commands.push(null);
        },
        removeDaliCommand: function (index) {
            this.commands.splice(index, 1);
        },
        sendDali: function () {
            this.$store.commit('spinner/SHOW');
            StandardDaliService.send(this.address, this.commands);
        },
    }
});
