import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardHeader, CCol, CForm, CInput, CRow, CSelect, } from '@coreui/vue/src';
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate';
import { integer, required } from 'vee-validate/dist/rules';
import InterfaceMappings from '../../components/Config/InterfaceMappings.vue';
import InterfacePorts from '../../components/Config/InterfacePorts.vue';
import FormErrorHandler from '../../helpers/FormErrorHandler';
import DaemonConfigurationService from '../../services/DaemonConfigurationService';
export default Vue.extend({
    name: 'IqrfUart',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardHeader: CCardHeader,
        CCol: CCol,
        CForm: CForm,
        CInput: CInput,
        CRow: CRow,
        CSelect: CSelect,
        InterfaceMappings: InterfaceMappings,
        InterfacePorts: InterfacePorts,
        ValidationObserver: ValidationObserver,
        ValidationProvider: ValidationProvider,
    },
    data: function () {
        return {
            componentName: 'iqrf::IqrfUart',
            configuration: {
                instance: null,
                IqrfInterface: null,
                baudRate: null,
                powerEnableGpioPin: null,
                busEnableGpioPin: null,
            },
            instance: null,
        };
    },
    computed: {
        baudRates: function () {
            var baudRates = [1200, 2400, 4800, 9600, 19200, 38400, 57600, 115200, 230400];
            return baudRates.map(function (baudRate) { return ({ value: baudRate, label: baudRate + ' Bd' }); });
        }
    },
    created: function () {
        extend('integer', integer);
        extend('required', required);
        this.getConfig();
    },
    methods: {
        getConfig: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            DaemonConfigurationService.getComponent(this.componentName)
                .then(function (response) {
                _this.$store.commit('spinner/HIDE');
                if (response.data.instances.length > 0) {
                    _this.configuration = response.data.instances[0];
                    _this.instance = _this.configuration.instance;
                }
            })
                .catch(function (error) { return FormErrorHandler.configError(error); });
        },
        saveConfig: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            if (this.instance !== null) {
                DaemonConfigurationService.updateInstance(this.componentName, this.instance, this.configuration)
                    .then(function () { return _this.successfulSave(); })
                    .catch(function (error) { return FormErrorHandler.configError(error); });
            }
            else {
                DaemonConfigurationService.createInstance(this.componentName, this.configuration)
                    .then(function () { return _this.successfulSave(); })
                    .catch(function (error) { return FormErrorHandler.configError(error); });
            }
        },
        successfulSave: function () {
            this.$store.commit('spinner/HIDE');
            this.$toast.success(this.$t('config.success').toString());
        },
        updateMapping: function (mapping) {
            Object.assign(this.configuration, mapping);
        },
        updatePort: function (port) {
            this.configuration.IqrfInterface = port;
        },
    },
    metaInfo: {
        title: 'config.iqrfUart.title',
    },
});
