import Vue from 'vue';
import IqrfNetService from '../../services/IqrfNetService';
export default Vue.extend({
    name: 'CoordinatorInfo',
    data: function () {
        return {
            enumeration: null,
            hasData: false,
            osInfo: null,
            trMcuType: null
        };
    },
    created: function () {
        var _this = this;
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type !== 'SOCKET_ONMESSAGE' ||
                mutation.payload.mType !== 'iqmeshNetwork_EnumerateDevice') {
                return;
            }
            try {
                var data = mutation.payload.data.rsp;
                _this.enumeration = data.peripheralEnumeration;
                _this.osInfo = data.osRead;
                _this.trMcuType = _this.osInfo.trMcuType;
                _this.hasData = true;
            }
            catch (e) {
                _this.hasData = false;
            }
        });
        if (this.$store.getters.isSocketConnected) {
            IqrfNetService.enumerateDevice(0);
        }
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
});
