import Vue from 'vue';
import { CButton, CCard, CCardBody, CForm, CInput } from '@coreui/vue/src';
import DaemonConfigurationService from '../../services/DaemonConfigurationService';
import FormErrorHandler from '../../helpers/FormErrorHandler';
export default Vue.extend({
    name: 'MainConfiguration',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CForm: CForm,
        CInput: CInput,
    },
    data: function () {
        return {
            configuration: {
                applicationName: null,
                resourceDir: null,
                dataDir: null,
                cacheDir: null,
                userDir: null,
                configurationDir: null,
                deploymentDir: null,
            },
        };
    },
    created: function () {
        this.getConfig();
    },
    methods: {
        getConfig: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            DaemonConfigurationService.getComponent('')
                .then(function (response) {
                _this.$store.commit('spinner/HIDE');
                _this.configuration = response.data;
            })
                .catch(function (error) { return FormErrorHandler.configError(error); });
        },
        saveConfig: function () {
            var _this = this;
            this.$store.commit('spinner/SHOW');
            DaemonConfigurationService.updateComponent('', this.configuration)
                .then(function () { return _this.successfulSave(); })
                .catch(function (error) { return FormErrorHandler.configError(error); });
        },
        successfulSave: function () {
            this.$store.commit('spinner/HIDE');
            this.$toast.success(this.$t('config.success').toString());
        },
    },
    metaInfo: {
        title: 'config.main.title',
    },
});
