import Vue from 'vue';
import { CButton, CButtonGroup } from '@coreui/vue/src';
import IqrfService from '../../services/IqrfService';
export default Vue.extend({
    name: 'InterfacePorts',
    components: {
        CButton: CButton,
        CButtonGroup: CButtonGroup,
    },
    props: {
        interfaceType: {
            type: String,
            required: true,
        },
    },
    data: function () {
        return {
            ports: [],
        };
    },
    created: function () {
        var _this = this;
        IqrfService.getInterfacePorts(this.interfaceType)
            .then(function (ports) { return (_this.ports = ports); })
            .catch(function () { return (_this.ports = []); });
    },
    methods: {
        setPort: function (port) {
            this.$emit('update-port', port);
        },
    },
});
