import Vue from 'vue';
import { CButton, CCard, CCardBody, CCardHeader } from '@coreui/vue/src';
import IqrfNetService from '../../services/IqrfNetService';
import ProductService from '../../services/IqrfRepository/ProductService';
import { timeout } from '../../helpers/timeout';
import RfModeLp from '../../assets/lp-black.svg';
import RfModeStd from '../../assets/std-black.svg';
export default Vue.extend({
    name: 'DeviceEnumeration',
    components: {
        CButton: CButton,
        CCard: CCard,
        CCardBody: CCardBody,
        CCardHeader: CCardHeader,
        RfModeLp: RfModeLp,
        RfModeStd: RfModeStd,
    },
    props: {
        address: {
            type: Number,
            required: false,
            default: 0,
        },
    },
    data: function () {
        return {
            response: undefined,
            osData: undefined,
            peripheralData: undefined,
            product: undefined,
        };
    },
    created: function () {
        var _this = this;
        if (this.$store.getters.isSocketConnected) {
            this.$store.commit('spinner/SHOW');
            IqrfNetService.enumerateDevice(this.address);
        }
        this.unsubscribe = this.$store.subscribe(function (mutation) {
            if (mutation.type === 'SOCKET_ONOPEN') {
                _this.$store.commit('spinner/SHOW');
                IqrfNetService.enumerateDevice(_this.address);
                return;
            }
            if (mutation.type === 'SOCKET_ONSEND' &&
                mutation.payload.mType !== 'iqmeshNetwork_EnumerateDevice') {
                _this.timeout = timeout('iqrfnet.enumeration.messages.failure', 30000);
            }
            if (mutation.type !== 'SOCKET_ONMESSAGE' ||
                mutation.payload.mType !== 'iqmeshNetwork_EnumerateDevice') {
                return;
            }
            clearTimeout(_this.timeout);
            _this.$store.commit('spinner/HIDE');
            var response = mutation.payload;
            if (response.data.status !== 0) {
                _this.$router.push('/iqrfnet/network/');
                _this.$toast.error(_this.$t('iqrfnet.enumeration.messages.failure').toString());
                return;
            }
            var data = response.data.rsp;
            if (data) {
                _this.response = data;
                _this.peripheralData = data.peripheralEnumeration;
                _this.osData = data.osRead;
                ProductService.get(_this.peripheralData.hwpId)
                    .then(function (response) {
                    _this.product = response.data;
                })
                    .catch(function (error) {
                    if (error.response !== undefined && error.response.status === 404) {
                        return;
                    }
                    _this.$toast.error(_this.$t('iqrfnet.enumeration.messages.repositoryUnavailable')
                        .toString());
                });
            }
        });
    },
    beforeDestroy: function () {
        this.unsubscribe();
    },
    metaInfo: {
        title: 'iqrfnet.enumeration.title',
    },
});
